@import './_search';
@import './reset';
@import url('https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap');
*, *:before, *:after {
    box-sizing: border-box; }

body {
  margin: 0;
  background-color: #e3e3e3; }

.loadContainer {
  font-size: 3vw;
  height: 30vh;
  display: flex;
  align-items: center; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Chivo', sans-serif;
  color: black;
  font-weight: 300; }

.main {
  display: grid;
  grid-template: {
    areas: "mainFore mainFore mainFore mainFore""week week week week" "sunrise moon wind humid";
    rows: 1.5fr 1fr 1.2fr; }
  grid-gap: 1em;
  height: 88vh;
  width: 50vw;
  animation: expand 500ms ease-in-out; }

.forecastItem {
  background-color: white;
  border-radius: 30px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); }

.MainForecast {
  grid-area: mainFore;
  display: flex;
  img {
    max-width: 60%;
    height: auto; } }

.Sunrise {
  grid-area: sunrise; }
.Humidity {
  grid-area: humid; }
.Wind {
  grid-area: wind; }
.MoonPhase {
  grid-area: moon; }
.WeeklyForecast {
  grid-area: week;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1em;
  padding: 5px;

  .weekItem {
    min-width: 50px;
    background-color: white;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    img {
      max-width: 60%;
      height: auto; } } }

.arrow {
  font-size: 100px;
  animation: arrow 1s linear; }


@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
@keyframes arrow {
  0% {
    transform: rotate(0deg); }
  100% {} }
@keyframes expand {
  0% {
    transform: translateY(100vw);
    opacity: 0; }
  50% {
    opacity: 1; }

  100% {
    transform: translateY(0); } }



@media screen and ( max-width: 1000px ) {
  .main {
    width: 90vw; }
  .search {
    width: 90vw; } }
