@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap);
.search{height:8vh;width:50vw;display:flex;align-items:flex-end;justify-content:flex-start;margin-bottom:2vh}.search input{background-color:transparent;border:none;width:300px;font-size:24pt;font-family:'Chivo', sans-serif}.search input:focus{outline:none}.search .searchSubmit{background-color:transparent;border:none;font-size:16pt}*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}*,*:before,*:after{box-sizing:border-box}body{margin:0;background-color:#e3e3e3}.loadContainer{font-size:3vw;height:30vh;display:flex;align-items:center}.App{display:flex;flex-direction:column;align-items:center;font-family:'Chivo', sans-serif;color:black;font-weight:300}.main{display:grid;grid-template-areas:"mainFore mainFore mainFore mainFore" "week week week week" "sunrise moon wind humid";grid-template-rows:1.5fr 1fr 1.2fr;grid-gap:1em;height:88vh;width:50vw;animation:expand 500ms ease-in-out}.forecastItem{background-color:white;border-radius:30px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.MainForecast{grid-area:mainFore;display:flex}.MainForecast img{max-width:60%;height:auto}.Sunrise{grid-area:sunrise}.Humidity{grid-area:humid}.Wind{grid-area:wind}.MoonPhase{grid-area:moon}.WeeklyForecast{grid-area:week;display:grid;grid-template-columns:repeat(5, 1fr);grid-gap:1em;padding:5px}.WeeklyForecast .weekItem{min-width:50px;background-color:white;border-radius:30px;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.WeeklyForecast .weekItem img{max-width:60%;height:auto}.arrow{font-size:100px;animation:arrow 1s linear}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes arrow{0%{transform:rotate(0deg)}100%{}}@keyframes expand{0%{transform:translateY(100vw);opacity:0}50%{opacity:1}100%{transform:translateY(0)}}@media screen and (max-width: 1000px){.main{width:90vw}.search{width:90vw}}

